"use client"
import { useState } from "react"
import { Settings2 } from "lucide-react"

export default function SettingsMenu() {
  const [isOpen, setIsOpen] = useState(false)
  const [temperature, setTemperature] = useState(0.5)
  const [model, setModel] = useState("mistral")
  const [maxTokens, setMaxTokens] = useState(512)
  const [llmRerank, setLlmRerank] = useState(true)
  const [tone, setTone] = useState("neutral")
  const [autoScroll, setAutoScroll] = useState(true)

  return (
    <div className="absolute top-4 left-4 z-50">
      <button
        className="p-2 rounded-full hover:bg-neutral-200 dark:hover:bg-neutral-700"
        onClick={() => setIsOpen(!isOpen)}
      >
        <Settings2 size={20} />
      </button>

      {isOpen && (
        <div className="mt-2 p-5 bg-white dark:bg-neutral-950 border border-neutral-200 dark:border-neutral-800 rounded-3xl shadow-lg w-72">
        {/* Tone Selector */}
          <div className="mb-7">
            <label className="block mb-2 text-sm font-medium text-neutral-700 dark:text-neutral-300">Tone</label>
            <div className="relative">
                <select
              value={tone}
              onChange={(e) => setTone(e.target.value)}
              className="
                    w-full 
                    appearance-none 
                    p-3 
                    rounded-full 
                    text-sm 
                    text-neutral-700 
                    dark:text-neutral-300
                    font-medium 
                    focus:outline-none 
                    border border-neutral-200 
                    dark:border-neutral-700 
                    hover:bg-neutral-100 
                    dark:hover:bg-neutral-800"
            >
              <option value="formal">Formal</option>
              <option value="neutral">Neutral</option>
              <option value="casual">Casual</option>
            </select>
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-3 text-neutral-500">
                <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
            </div>
            </div>
          </div>

            {/* Model Selector */}
            <div className="mb-7">
                <label className="block mb-2 text-sm font-medium text-neutral-700 dark:text-neutral-300">Model</label>
                <div className="relative">
                    <select
                    value={model}
                    onChange={(e) => setModel(e.target.value)}
                    className="
                    w-full 
                    appearance-none 
                    p-3 
                    rounded-full 
                    text-sm 
                    text-neutral-700 
                    dark:text-neutral-300
                    font-medium 
                    focus:outline-none 
                    border border-neutral-200 
                    dark:border-neutral-700 
                    hover:bg-neutral-100 
                    dark:hover:bg-neutral-800"
                    >
                    <option value="mistral">Mistral</option>
                    </select>
                    <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-3 text-neutral-500">
                        <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                        </svg>
                    </div>
                </div>
            </div>

          {/* Temperature Slider */}
          <div className="mb-7">
            <label className="block mb-2 text-sm font-medium text-neutral-700 dark:text-neutral-300">Temperature</label>
            <input
              type="range"
              min="0"
              max="1"
              step="0.01"
              value={temperature}
              onChange={(e) => setTemperature(parseFloat(e.target.value))}
              className="w-full h-7 appearance-none rounded-full bg-transparent cursor-pointer"
              style={{
              background: `linear-gradient(to right, #22c55e ${temperature * 100}%, #e5e7eb ${temperature * 100}%)`,
              }}
            />
            <div className="flex justify-between mt-1 text-xs text-neutral-400">
                <span>Precise</span>
                <span>Creative</span>
            </div>
          </div>

          {/* LLM Rerank Toggle (Modern Switch) */}
            <div className="flex justify-between items-center mb-5">
            <label className="text-sm font-medium text-neutral-800 dark:text-neutral-200">
                LLM Reranking (Slower)
            </label>
            <button
                onClick={() => setLlmRerank(!llmRerank)}
                className={`w-11 h-7 flex items-center rounded-full p-1 transition-colors duration-300
                ${llmRerank ? "bg-green-500" : "bg-neutral-200"}`}
            >
                <div
                className={`w-5 h-5 bg-white rounded-full shadow-md transform transition-transform duration-300
                    ${llmRerank ? "translate-x-4" : "translate-x-0"}`}
                />
            </button>
            </div>


          {/* Autoscroll Toggle */}
          <div className="flex justify-between items-center">
            <label className="text-sm font-medium text-neutral-700 dark:text-neutral-300">
            Autoscroll
            </label>
            <button
                onClick={() => setAutoScroll(!autoScroll)}
                className={`w-11 h-7 flex items-center rounded-full p-1 transition-colors duration-300
                ${autoScroll ? "bg-green-500" : "bg-neutral-200"}`}
            >
                <div
                className={`w-5 h-5 bg-white rounded-full shadow-md transform transition-transform duration-300
                    ${autoScroll ? "translate-x-4" : "translate-x-0"}`}
                />
            </button>
          </div>
        </div>
      )}
    </div>
  )
}
